<template>
  <form @submit.p.prevent="login()">
    <label>

      <input type="text" placeholder="id" v-model="id">
    </label>
    <label>
      <input type="password" placeholder="password" v-model="passwd">

    </label>

    <button type="submit">Login</button>

  </form>


</template>


<script>

import apiInquiry from '../api/api.js'

export default {
  data() {
    return {
      loginSuccess: false,
      loginError: false,
      id: '',
      passwd: ''
    }
  },
  methods: {
    login() {
      apiInquiry.getLogin(this.id, this.passwd).then(res=>{
        console.log("result", res)
      });
    }
  }
}


</script>



